import React, { useEffect, useState } from "react";
import { motion } from "framer-motion";
import { useNavigate } from "react-router-dom";
import "./VoiceFeatures.css";

const VoiceFeatures = () => {
  const [features, setFeatures] = useState([]);
  const [voiceMode, setVoiceMode] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    fetch("http://localhost:5500/api/voicefeatures")
      .then((res) => res.json())
      .then((data) => setFeatures(data))
      .catch((err) => console.log(err));
  }, []);

  const handleAction = (actionType) => {
    if (actionType === "tts") {
      const msg = new SpeechSynthesisUtterance(
        "Hi! I'm EmiBot. Let's have a voice conversation!"
      );
      window.speechSynthesis.speak(msg);
    } else if (actionType === "stt") {
      const SpeechRecognition =
        window.SpeechRecognition || window.webkitSpeechRecognition;
      if (!SpeechRecognition)
        return alert("Your browser does not support Speech Recognition.");
      const recognition = new SpeechRecognition();
      recognition.onresult = (event) => {
        const transcript = event.results[0][0].transcript;
        navigate("/voice-interaction", { state: { transcript } });
      };
      recognition.start();
    } else if (actionType === "toggle") {
      setVoiceMode(!voiceMode);
      alert(`Voice mode is now ${!voiceMode ? "ENABLED" : "DISABLED"}`);
    }
  };

  return (
    <div className="voice-section">
      <motion.h1
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
      >
        Interact with Your Voice
      </motion.h1>

      <motion.p
        className="subtitle"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        style={{ color: "white" }}
      >
        Experience the freedom of voice interactions with EmiBot.
      </motion.p>

      {features.map((feature, i) => (
        <motion.div
          className={`feature-row ${i % 2 === 1 ? "reverse" : ""}`}
          key={i}
          initial={{ opacity: 0, x: i % 2 === 0 ? -100 : 100 }}
          whileInView={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.8, delay: i * 0.2 }}
          viewport={{ once: true }}
        >
          {feature.image && (
            <motion.img
              src={feature.image}
              alt={feature.title}
              whileHover={{ scale: 1.05 }}
            />
          )}
          <motion.div className="feature-content">
            <h3>{feature.title}</h3>
            <p>{feature.description}</p>
            {feature.button && (
              <motion.button
                className="voice-btn"
                onClick={() => handleAction(feature.actionType)}
                whileHover={{ scale: 1.1 }}
                whileTap={{ scale: 0.95 }}
              >
                {feature.button}
              </motion.button>
            )}
          </motion.div>
        </motion.div>
      ))}
    </div>
  );
};

export default VoiceFeatures;
