
import React, { useEffect, useState } from "react";
import "./Testimonilas.css";

const Testimonials = () => {
  const [testimonials, setTestimonials] = useState([]);

  useEffect(() => {
    fetch("http://localhost:5500/api/testimonials")
      .then(res => res.json())
      .then(data => setTestimonials(data))
      .catch(err => console.error("Error fetching testimonials:", err));
  }, []);

  return (
    <div className="testimonials-section">
      <h1 className="testimonials-title">What Our Users Are Saying</h1>
      <p className="testimonials-subtitle">
        Hear from our community of users who have experienced the transformative 
        capabilities of EmiBot in their daily lives.
      </p>

      <div className="testimonials-grid">
        {testimonials.map((item, index) => (
          <div className="testimonial-card" key={index}>
            <img src={item.image} alt={item.title} />
            <h3>{item.title}</h3>
            <p>{item.description}</p>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Testimonials;
