import React from "react";
import "./Memory.css";

const Memory = () => {
  const cards = [
    {
      img: "https://t4.ftcdn.net/jpg/04/11/50/99/360_F_411509944_NHQwlYfg1td6fBQyyHLdlfltmlv8cmAp.jpg",
      title: "Pinning Key Facts",
      date: "August 20, 2025",
      desc: "You can pin significant information such as birthdays and favorite activities, enabling EmiBot to remember key details."
    },
    {
      img: "https://images.unsplash.com/photo-1522202176988-66273c2fd55f",
      title: "Storing Conversations",
      date: "August 20, 2025",
      desc: "EmiBot stores your past conversations securely, allowing it to recall important context and details."
    },
    {
      img: "https://t4.ftcdn.net/jpg/14/23/60/73/360_F_1423607388_5peSA2L19ahf5jJbBZwLy2zhv8ztjb77.jpg",
      title: "Seamless Contextual Recall",
      date: "August 20, 2025",
      desc: "With seamless contextual recall, EmiBot ensures that your chats feel continuous and connected."
    }
  ];

  return (
    <div className="memory-section">
      <h1>Memory That Matters</h1>
      <p className="subtitle">
        Discover how EmiBot retains important details for a more meaningful connection.
      </p>
      <div className="memory-grid">
        {cards.map((card, index) => (
          <div className="memory-card" key={index}>
            <img src={card.img} alt={card.title} />
            <div className="card-content">
              <h3>{card.title}</h3>
              <span className="date">{card.date}</span>
              <p>{card.desc}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Memory;
