import { Helmet } from "react-helmet";
import React from "react";
import "./Featuress.css";
import { motion } from "framer-motion";
import Memory from "../Memory/Memory";
import VoiceFeatures from "../Voivefeature/VoiceFeatures";

const Featuress = () => {
  const featureVariants = {
    hidden: { opacity: 0, y: 50 },
    visible: (i) => ({
      opacity: 1,
      y: 0,
      transition: { delay: i * 0.3, duration: 0.6, ease: "easeOut" }
    })
  };

  const gridVariants = {
    hidden: { opacity: 0, scale: 0.8 },
    visible: (i) => ({
      opacity: 1,
      scale: 1,
      transition: { delay: i * 0.2, duration: 0.6, ease: "easeOut" }
    })
  };

  return (
    <>
    <Helmet>
  <title>Features | EmiBot AI Companion</title>
  <meta name="description" content="Discover all features of Your App." />
</Helmet>
  <div class="body">


   

    
      {/* Features Section */}
      <div className="features-section">
        {[
          {
            img: "https://t4.ftcdn.net/jpg/04/11/50/99/360_F_411509944_NHQwlYfg1td6fBQyyHLdlfltmlv8cmAp.jpg",
            title: "Natural Language Processing",
            desc: "EmiBot utilizes advanced natural language processing to understand and respond to your queries in a conversational manner, making every interaction feel personal and engaging."
          },
          {
            img: "https://cdn.prod.website-files.com/666eb11a863bd950676f05ee/6784dce55be20890ac6aec38_Edge-AI----What-Is-an-Example-of-Contextual-Awareness-01.jpg",
            title: "Contextual Awareness",
            desc: "With contextual awareness, EmiBot remembers your previous discussions, allowing for a more coherent and relevant conversation flow."
          },
          {
            img: "https://media.istockphoto.com/id/1456618360/photo/nonstop-service-24-hr-concept-businessman-hand-holding-virtual-24-7-with-clock-on-hand-for.jpg?s=612x612&w=0&k=20&c=z05AD3X-RQDrArao9YwotjzyDhK-1lHtXiIRY2DXw8o=",
            title: "24/7 Availability",
            desc: "EmiBot is always available to chat, ensuring you receive support and companionship whenever you need it."
          }
        ].map((feature, index) => (
          <motion.div
            className={`feature-row ${index % 2 === 1 ? "reverse" : ""}`}
            key={index}
            variants={featureVariants}
            initial="hidden"
            whileInView="visible"
            viewport={{ once: true, amount: 0.2 }}
            custom={index}
          >
            <div className="feature-image">
              <img src={feature.img} alt={feature.title} />
            </div>
            <div className="feature-text">
              <h2 className="smoking-text">{feature.title}</h2>

              <p>{feature.desc}</p>
            </div>
          </motion.div>
        ))}
      </div>

      {/* Personalize Section */}
      <div className="personalize-section">
      <h1 style={{ color: "white" }}>Personalize Your Experience</h1>
<p style={{ color: "wheat" }}>
  Make EmiBot truly yours with customizable avatars and wallpapers.
</p>


        <div className="personalize-grid">
          {[
            "https://static.vecteezy.com/system/resources/previews/024/306/628/non_2x/talking-cute-robot-with-artificial-intelligence-concept-of-chatbot-ai-generated-image-photo.JPG",
            "https://img.lovepik.com/photo/48011/3618.jpg_wh860.jpg",
            "https://thumbs.dreamstime.com/b/phone-view-front-empty-screen-robot-hand-d-illustration-phone-view-front-robot-hand-101483599.jpg",
            "https://blog.qburst.com/wp-content/uploads/2020/09/Conversational-AI-Chatbot-Architecture-Overview.png",
            "https://plus.unsplash.com/premium_photo-1681010317870-44a7ca6f9bee?fm=jpg&q=60&w=3000",
            "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR8J9GzvCfpopoBTMQYjXugr3qbpURZcktSrg&s"
          ].map((src, i) => (
            <motion.div
              className="grid-item"
              key={i}
              variants={gridVariants}
              initial="hidden"
              whileInView="visible"
              viewport={{ once: true, amount: 0.2 }}
              custom={i}
            >
              <img src={src} alt={`Custom Avatar ${i + 1}`} />
            </motion.div>
          ))}
        </div>
      </div>

      <Memory />
      <VoiceFeatures />
      </div>
    </>
    
  );
};

export default Featuress;
