import React, { useState } from "react";
import "./Chatbot.css";
import { db } from "../../Firebaseconfig";
import { collection, addDoc, Timestamp } from "firebase/firestore";

const Chatbot = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [message, setMessage] = useState("");
  const [chatLog, setChatLog] = useState([]);
  const [userName, setUserName] = useState(""); // store user's name

  const predefinedQA = {
    "what can emibot do": "EmiBot can chat, suggest features, remember preferences, and help you connect with the community.",
    "how do i customize emibot": "You can go to the Customize page and personalize your avatar, theme, and interactions.",
    "is emibot safe for kids": "Yes, EmiBot is designed to provide safe, monitored interactions suitable for all users.",
    "can emibot help me learn": "Absolutely! EmiBot can suggest learning resources, tips, and track your progress.",
    "does emibot remember my preferences": "Yes, EmiBot stores your choices to provide personalized experiences."
  };

  const suggestedQuestions = Object.keys(predefinedQA);

  const handleNext = async (customMessage = null) => {
    const input = customMessage || message;
    if (!input.trim()) return;

    const userMessage = input.trim().toLowerCase();
    let answer = "";

    // Personalized greeting logic
    if (userMessage === "hi" || userMessage === "hello" || userMessage.includes("how are you")) {
      answer = !userName
        ? "Hello! What's your name?"
        : `Hi ${userName}! I'm good, thank you. How about you?`;
    } 
    else if (userMessage.startsWith("my name is")) {
      const name = input.trim().split("my name is")[1]?.trim();
      if (name) {
        setUserName(name);
        answer = `Nice to meet you, ${name}! How can I help you today?`;
      } else {
        answer = "I didn't catch your name. Could you repeat it?";
      }
    }
    else if (!userName && /^[a-zA-Z]+$/.test(userMessage)) {
      setUserName(input.trim());
      answer = `Nice to meet you, ${input.trim()}! How can I help you today?`;
    }
    else {
      // Keyword-based matching
      if (userMessage.includes("emibot") && userMessage.includes("do"))
        answer = predefinedQA["what can emibot do"];
      else if (userMessage.includes("customize"))
        answer = predefinedQA["how do i customize emibot"];
      else if (userMessage.includes("safe") || userMessage.includes("kids"))
        answer = predefinedQA["is emibot safe for kids"];
      else if (userMessage.includes("learn"))
        answer = predefinedQA["can emibot help me learn"];
      else if (userMessage.includes("remember") || userMessage.includes("preferences"))
        answer = predefinedQA["does emibot remember my preferences"];
      else
        answer = "Sorry, I don't have an answer for that yet. We'll get back to you!";
    }

    console.log("User:", input, "Bot:", answer);

    setChatLog(prev => [...prev, { question: input, answer }]);
    setMessage("");

    try {
      await addDoc(collection(db, "chatbotLogs"), {
        question: input,
        answer,
        timestamp: Timestamp.now(),
      });
    } catch (err) {
      console.error("Error saving to Firebase:", err);
    }
  };

  return (
    <div className="chatbot-wrapper">
      {!isOpen && (
        <button className="chatbot-toggle" onClick={() => setIsOpen(true)}>
          ðŸ¤–
        </button>
      )}

      {isOpen && (
        <div className="chatbot-box">
          <div className="chatbot-header">
            <h4>Ask EmiBot AI anything!</h4>
            <button className="close-btn" onClick={() => setIsOpen(false)}>âœ•</button>
          </div>

          {/* Suggested Questions */}
          <div className="chatbot-suggestions">
            {suggestedQuestions.map((q, idx) => (
              <button 
                key={idx} 
                className="suggestion-btn"
                onClick={() => handleNext(q)}
              >
                {q}
              </button>
            ))}
          </div>

          <div className="chatbot-messages">
            {chatLog.map((chat, idx) => (
              <div key={idx}>
                <div className="chat-msg user"><strong>You:</strong> {chat.question}</div>
                <div className="chat-msg bot"><strong>EmiBot:</strong> {chat.answer}</div>
              </div>
            ))}
          </div>

          <div className="chatbot-input">
            <input
              type="text"
              placeholder="Type your question..."
              value={message}
              onChange={(e) => setMessage(e.target.value)}
              onKeyDown={(e) => e.key === "Enter" && handleNext()}
            />
            <button onClick={() => handleNext()}>ASK</button>
          </div>
        </div>
      )}
    </div>
  );
};

export default Chatbot;
