import { Helmet } from "react-helmet";
import React from "react";
import "./Customize.css";
import PageWrapper from "../../pages/PageWrapper";
import { Link } from "react-router-dom";


const Customize = () => {
  return (
    <>
    <Helmet>
  <title>Customize Bot | EmiBot AI Companion</title>
  <meta name="description" content="Customize your chatbot profile, mood, and behavior." />
</Helmet>
  
    <PageWrapper>
    <div className="customize-section">
      <div className="customize-content">
        <h2>Customize Your Experience</h2>
        <p>
          EmiBot offers a range of customizable avatars and wallpapers. Tailor
          the look of your bot and chat interface to reflect your unique style
          and preferences, creating a more immersive experience.
        </p>
     <Link to="/featuress" className="explore-btn">
  Explore Options
</Link>

      </div>

      <div className="customize-image">
        <img src="https://cdn.fstoppers.com/styles/large-16-9/s3/lead/2022/12/openai-gpt-adobe-after-effects-creative-code-monitor.jpg" alt="Customize EmiBot" />
      </div>
    </div>
    </PageWrapper>
      </>
  );
};

export default Customize;
