// src/components/Background/Background.jsx
import React, { useEffect, useRef } from "react";

const Background = () => {
  const canvasRef = useRef(null);

  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");

    function resizeCanvas() {
      canvas.width = canvas.parentElement.offsetWidth;
      canvas.height = canvas.parentElement.offsetHeight;
    }

    resizeCanvas();
    window.addEventListener("resize", resizeCanvas);

    const colors = ["#FF7C4F", "#FFB26B", "#6C5DD3", "#36D1DC", "#FF3CAC", "#FFD700", "#00FFAA"];
    const waveCount = 5;
    const waves = [];

    for (let i = 0; i < waveCount; i++) {
      waves.push({
        yOffset: canvas.height / 2 + i * 40,
        amplitude: 30 + Math.random() * 30,
        wavelength: 150 + Math.random() * 100,
        speed: 0.01 + Math.random() * 0.02,
        phase: Math.random() * Math.PI * 2,
        color: colors[i % colors.length],
      });
    }

    function animate() {
      // Black background
      ctx.fillStyle = "#000000";
      ctx.fillRect(0, 0, canvas.width, canvas.height);

      waves.forEach(wave => {
        wave.phase += wave.speed;

        for (let x = 0; x < canvas.width; x += 6) {
          const y = wave.yOffset + Math.sin((x / wave.wavelength) * Math.PI * 2 + wave.phase) * wave.amplitude;

          // Multicolor glow
          const gradient = ctx.createRadialGradient(x, y, 0, x, y, 6);
          gradient.addColorStop(0, wave.color + "FF");   // opaque color at center
          gradient.addColorStop(1, wave.color + "55");   // fading edge

          ctx.fillStyle = gradient;
          ctx.beginPath();
          ctx.arc(x, y, 3, 0, Math.PI * 2);
          ctx.fill();
        }
      });

      requestAnimationFrame(animate);
    }

    animate();

    return () => window.removeEventListener("resize", resizeCanvas);
  }, []);

  return (
    <canvas
      ref={canvasRef}
      style={{ 
        position: "absolute", 
        top: 0, 
        left: 0, 
        width: "100%", 
        height: "100%", 
        zIndex: -1 
      }}
    />
  );
};

export default Background;
